@use "~admin-stylesheets/colors";

.tested-url-with-query-args-scope {

	&__block {
		margin-bottom: 1em;
	}

	&__argument:not(:first-child) {
		position: relative;
		margin-top: 9px;
		padding-top: 14px;
		border-top: 1px solid colors.$color__border-editor-light;

		&::before {
			content: attr(data-operator);
			display: block;
			background: #fff;
			position: absolute;
			top: -10px;
			left: 50%;
			text-transform: uppercase;
			transform: translateX(-50%);
			padding: 0 1em;
			font-size: 10px;
			font-weight: 600;
		}

		&:last-child::before {
			display: none;
		}
	}

	&__argument-value > * {
		margin-bottom: 15px;
	}

	&__argument-setup {
		display: flex;
		gap: 0.5em;

		select {
			min-width: 11em;
		}
	}

	&__argument-error {
		color: colors.$color__text-red;
		font-size: 0.9em;
		line-height: 1.2;
	}
}
