@use "~admin-stylesheets/icons";

.nab-conversion-action-type-new {
	display: flex;
	justify-content: center;

	&__list button {

		svg {
			width: 1em;
			height: 1em;
			font-size: 20px;
		}

		.icon_svg__black {
			fill: currentcolor !important;
		}

		.icon_svg__grey {
			fill: currentcolor !important;
		}

		.icon_svg__white {
			fill: #fff !important;
		}
	}
}
