@use "~admin-stylesheets/colors";

.nab-edit-experiment-php-scope {

	&__hook {
		margin-bottom: 1em;
	}

	&__snippet {
		margin-bottom: 1.5em !important;
		overflow-y: auto;
		max-height: 20em;
	}

	&__snippet-error {
		color: colors.$color__text-red;
		margin-top: -1.2em !important;
		margin-bottom: 2em;
	}

	&__preview-error {
		color: colors.$color__text-red;
		margin-top: -0.2em !important;
	}
}
